import log4js from "log4js";
import aqp from "api-query-params";
import User, { schemaCreate, schemaUpdate, schemaLogin } from "./model";
import { success, fail, notFound, hasProp, hash } from "../../lib";
import { userAuthenticate } from "../../services";
import { genCode } from "../../lib/helpers";

// Logging
const logger = log4js.getLogger("[user]");
log4js.configure({
    appenders: { file: { type: "file", filename: "logs/user.log" } },
    categories: { default: { appenders: ["file"], level: "debug" } },
});

export async function fetchRecord(req, res) {
    const { query } = req;
    const { filter, skip, limit, sort, projection } = aqp(query);
    try {
        const result = await User.find(filter)
            .populate("notifications")
            .populate("transactions")
            .skip(skip)
            .limit(limit)
            .sort(sort)
            .select(projection)
            .exec();
        if (!result) {
            return notFound(res, "Error: Bad Request: Model not found");
        }
        logger.info("SUCCESS", []);
        return success(res, 201, result, null);
    } catch (err) {
        logger.error(err);
        return fail(res, 500, `Error retrieving record. ${err.message}`);
    }
}

// eslint-disable-next-line complexity
export async function createRecord(req, res) {
    const data = req.body;
    data.api_key = genCode(32).toLowerCase();
    if (hasProp(data, "password")) data.password = hash(req.body.password);
    const { error } = schemaCreate.validate(data);
    if (error) return fail(res, 422, `Error validating request data. ${error.message}`);
    const { email, phone } = data;
    const duplicate = await User.findOne({ $or: [{ email }, { phone }] }).exec();
    if (duplicate) {
        return fail(res, 422, `Error! Record already exist for ${email} or ${phone}`);
    }
    const newRecord = new User(data);
    try {
        const result = await newRecord.save();
        if (!result) {
            logger.info("SUCCESS", []);
            return notFound(res, "Error: Bad Request: Model not found");
        }
        return success(res, 201, result, "Record created successfully!");
    } catch (err) {
        logger.error(err);
        return fail(res, 500, `Error creating record. ${err.message}`);
    }
}

export async function updateRecord(req, res) {
    const data = req.body;
    const { recordId: id } = req.params;
    if (hasProp(data, "password")) data.password = hash(req.body.password);
    const { error } = schemaUpdate.validate(data);
    if (error) return fail(res, 422, `Error validating request data. ${error.message}`);
    try {
        const result = await User.findOneAndUpdate({ _id: id }, data, { new: true });
        if (!result) {
            return notFound(res, `Bad Request: Model not found with id ${id}`);
        }
        return success(res, 200, result, "Record updated successfully!");
    } catch (err) {
        logger.error(err);
        return fail(res, 500, `Error updating record. ${err.message}`);
    }
}

export async function deleteRecord(req, res) {
    const { recordId: id } = req.params;
    try {
        const result = await User.findOneAndRemove({ _id: id });
        if (!result) {
            return notFound(res, `Bad Request: Model not found with id ${id}`);
        }
        return success(res, 204, result, "Record deleted successfully!");
    } catch (err) {
        logger.error(err);
        return fail(res, 500, `Error deleting record. ${err.message}`);
    }
}

export async function login(req, res) {
    console.log(req.body);
    const { error } = schemaLogin.validate(req.body);
    if (error) return fail(res, 422, `Error validating request data. ${error.message}`);
    return userAuthenticate(req.body)
        .then(userToken => success(res, 201, userToken, "Login was successful!"))
        .catch(err => fail(res, 403, `Authentication failed  ${err.message}`));
}
